const coockies = document.querySelector('.jsCoockies');
const coockiesButton = document.querySelector('.jsCoockiesButton');

window.openCoockies = function(){
    const coockies = document.querySelector('.jsCoockies');
    coockies.classList.add('is-open');
}

window.closeCoockies = function(){
    const coockies = document.querySelector('.jsCoockies');
    coockies.classList.remove('is-open');
    // $.cookie('COOKIE_AGREE', 'Y',{ expires: 14 });
}

document.addEventListener("DOMContentLoaded", function() {
    const coockiesButton = document.querySelector('.jsCoockiesButton');

    setTimeout(function(){window.openCoockies()} , 2000);
        coockiesButton.addEventListener('click', function(){
        window.closeCoockies()
    })
})